.responsive-wrapper {
	width: 95%;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
  p{
    font-family: initial;
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    font-size: 1.5rem;
    @media screen and (max-width:$mobile) {
      font-size: 1.1rem;
    }  
  }

  &__page-title {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    h1 {
      font-size: 1.75rem;
      font-family: "Fraunces", serif;
      font-weight: 700;
      color: $semi-dark;
      text-align: center;
      position: relative;     
      &:after {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        bottom: -3px;
        height: 2px;
        background-color: currentcolor;
      }
    }
  }
  &__magazine-layout {
    margin-top: 3rem;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-row-gap: 2rem;
  }
}